.Con{
    background-color: #afafaf;
    display: grid;
    grid-template-columns: 1fr 2fr 3fr 3fr 1fr;
    align-items: baseline;
    padding: 10px;
   
}

.brand_logo{
    width: 4rem;
    
}
.line{
    
    padding: 0px;
    margin: 0px;
    color: white;

}
.Logo_box{
    grid-column: 2/3;

}
.navigations p{
    padding: 0.2rem;
    color: white;
    font-weight: 500;
}
.navigations{

    grid-column: 4/5;
    display: flex;
    justify-content: space-between;
}